<template>
	<div class="s_container">
		<h3><a href="https://en.wikipedia.org/wiki/5G" target="_blank">loading</a></h3>
		<div class='search_area'>
			<!-- <el-button style="margin-top: 15px;" @click="query"></el-button> -->
