					</li>
					<li>
						<div class='label_font'><a href="" target="_blank"></a></div>
						<div class='abstract'>
							<p><img src=""></p>
							<div class="clearfix"></div>
						</div>

					</li>
					<li>
						<div class='label_font'><a href="" target="_blank"></a></div>
						<div class='abstract'>
							<p><img src=""></p>
							<div class="clearfix"></div>
						</div>
					</li>
					<li>
						<div class='label_font'><a href="" target="_blank"></a></div>
						<div class='abstract'>
							<p><img src=""></p>
							<div class="clearfix"></div>
						</div>

					</li>
					<li>
						<div class='label_font'><a href="" target="_blank"></a></div>
						<div class='abstract'>
							<p><img src=""></p>
							<div class="clearfix"></div>
						</div>
					</li>
					<li>
						<div class='label_font'><a href="" target="_blank"></a></div>
						<div class='abstract'>
							<p><img src=""></p>
							<div class="clearfix"></div>
						</div>
					</li>
				</ol>
			</div>
		</div>
	</div>
</template>

<script>
	import * as d3 from 'd3'
	export default {
		name: 'search',
		props: {

		},
		data() {
			return {
				input: "",
				input_results: [],
				data: require('../../data/test1.json'),
				label: require('../../data/population.json'),
				colors: ['#BA55D3', '#55cccc', '#d0d066', '#ff8c8e', '#32a7b1', '#59aa40', '#1E90FF', '#59aa40', '#ff8c8e',
					'#ca635f', '#a5a5a5'
				],
				labels: ["", "", "", "null", "", ""]
			}
		},
		watch: {
			// 
			data(newData, oldData) {
				console.log(newData, oldData)
				// svg
				d3.select("#svg_left").selectAll("g").remove()
				this.create_graph()
				// d3.select("#svg_right").selectAll("g").remove()
				// this.create_label(this.label, {
				// 	name: d => d.name,
				// 	value: d => 1000,
				// 	width: 500,
				// 	height: 500
				// })
			}
		},
		methods: {
			loadAll() {
				// console.dir(this.input)
				var res = this.getallnodes(1000).then((result) => {
					this.input_results = result
					// console.log("")
					// console.log(this.input_results)
				})

			},
			query() {
				console.log("")
				var res_length = 0
				this.GetNeo4jData(1000, this.input).then((length) => {
					res_length = length
					console.log("sada")
					console.log(res_length)
					if (res_length >= 1) {
						const h = this.$createElement;
						this.$message({
							message: h('p', null, [
								h('span', null, ', :  '),
								h('i', {
									style: 'color: teal'
								}, res_length)
							]),
							center: true,
							type: 'success'
						});
					} else {
						const h = this.$createElement;
						this.$message({
							message: h('p', null, [
								h('span', {
									style: 'color:teal'
								}, ' :  '),
								h('i', {
									style: 'color: red'
								}, "")
							]),
							center: true,
							type: 'error'
						});
					}
				})
				this.wiki_api(this.input)
			},
			async getallnodes(limit_items) {
				const db = { // 
					// url: 'bolt://127.0.0.1:7687/', // bolt localhost
					url: 'bolt://123.56.133.187:7687/', //httphttps
					user: 'neo4j', // 		(neo4j)
					password: 'neo4jneo4j' // 		ne4oj
				}
				const start = new Date()
				const neo4j = require('neo4j-driver')
				const driver = neo4j.driver(db.url, neo4j.auth.basic(db.user, db.password))
				const session = driver.session()
				const label_result = await session.run(
					'CALL db.labels() '
				);
				var label_number = label_result.records.length
				var label = []

				for (let i = 0; i < label_number; i++) {
					label.push(label_result.records[i]._fields[0])
				}


				var node_result = []
				for (let i = 0; i < label.length; i++) {
					var tmp = await session.run(
						'MATCH (p:' + label[i] + ') RETURN p ' +
						'LIMIT $limit_items ', {
							limit_items: neo4j.int(limit_items)
						}
					);
					for (let j = 0; j < tmp.records.length; j++) {
						let t = {}
						t["value"] = tmp.records[j]._fields[0].properties.name
						node_result.push(t)
					}
				}
				return node_result
			},

			querySearch(queryString, cb) {
				var res = this.input_results
				var results = queryString ? res.filter(this.createFilter(queryString)) : res
				//  callback 
				cb(results)
			},
			createFilter(queryString) {
				return (res) => {
					return (res.value.toLowerCase().indexOf(queryString.toLowerCase()) !== -1)
				}
			},
			handleSelect(item) {
				console.log(item)
			},
			input_clear() {
				this.$message({
					message: '',
					center: true,
				});
			},
			addMarkers() {
				// 
				var svgDom = d3.select("#svg_left")
				var defs = svgDom.append("defs")
				const posMarker = defs.append("marker")
					.attr("id", "posMarker")
					.attr("orient", "auto")
					.attr("stroke-width", 2)
					.attr("markerUnits", "strokeWidth")
					.attr("markerUnits", "strokeWidth")
					.attr("viewBox", "0 -5 400 400")
					.attr("refX", 190)
					.attr("refY", 30)
					.attr("markerWidth", 20)
					.attr("markerHeight", 20)
					.append("path")
					//.attr("d", "M 0 -5 L 10 0 L 0 5")
					.attr("d", "M 0 0 L 30 30 L 0 60 ")
					.attr('fill', '#b4b5a9')
					.attr("stroke-opacity", 1);
				// const posActMarker = defs.append("marker")   //
				//   .attr("id", "posActMarker")
				//   .attr("orient", "auto")
				//   .attr("stroke-width", 2)
				//   .attr("markerUnits", "strokeWidth")
				//   .attr("markerUnits", "userSpaceOnUse")
				//   .attr("viewBox", "0 -5 10 10")
				//   .attr("refX", 31)
				//   .attr("refY", 0)
				//   .attr("markerWidth", 12)
				//   .attr("markerHeight", 12)
				//   .append("path")
				//  .attr("d","M 0 5 L 8 0 L 0 -5")
				//   .attr('fill', '#ffd918')
				//   .attr("stroke-opacity", 0.6);
				// const negMarker = defs.append("marker")
				//   .attr("id","negMarker")
				//   .attr("orient","auto")
				//   .attr("stroke-width",2)
				//   .attr("markerUnits", "strokeWidth")
				//   .attr("markerUnits", "userSpaceOnUse")
				//   .attr("viewBox", "0 -5 10 10")
				//   .attr("refX", -25)
				//   .attr("refY", 0)
				//   .attr("markerWidth", 12)
				//   .attr("markerHeight", 12)
				//   .append("path")
				//   .attr("d", "M 10 -5 L 0 0 L 10 5")
				//   .attr('fill', '#999')
				//   .attr("stroke-opacity", 0.6);
			},
			create_label(data, {
				name = ([x]) => x, // given d in data, returns the (ordinal) label
				value = ([, y]) => y, // given d in data, returns the (quantitative) value
				title, // given d in data, returns the title text
				width = 640, // outer width, in pixels
				height = 400, // outer height, in pixels
				innerRadius = Math.min(width, height) / 3, // inner radius of pie, in pixels (non-zero for donut)
				// innerRadius = 0, // inner radius of pie, in pixels (non-zero for donut)
				outerRadius = Math.min(width, height) / 2, // outer radius of pie, in pixels
				labelRadius = (innerRadius + outerRadius) / 2, // center radius of labels
				format = ",", // a format specifier for values (in the label)
				names, // array of names (the domain of the color scale)
				colors, // array of colors for names
				stroke = innerRadius > 0 ? "none" : "white", // stroke separating widths
				strokeWidth = 1, // width of stroke separating wedges
				strokeLinejoin = "round", // line join of stroke separating wedges
				padAngle = stroke === "none" ? 1 / outerRadius : 0, // angular separation between wedges
			} = {}) {
				// Compute values.
				const N = d3.map(data, name);
				const V = d3.map(data, value);
				const I = d3.range(N.length).filter(i => !isNaN(V[i]));

				// Unique the names.
				if (names === undefined) names = N;
				names = new d3.InternSet(names);

				// Chose a default color scheme based on cardinality.
				if (colors === undefined) colors = d3.schemeSpectral[names.size];
				if (colors === undefined) colors = d3.quantize(t => d3.interpolateSpectral(t * 0.8 + 0.1), names.size);

				// Construct scales.
				const color = d3.scaleOrdinal(names, colors);

				// Compute titles.
				if (title === undefined) {
					const formatValue = d3.format(format);
					title = i => `${N[i]}\n${formatValue(V[i])}`;
				} else {
					const O = d3.map(data, d => d);
					const T = title;
					title = i => T(O[i], i, data);
				}

				// Construct arcs.
				const arcs = d3.pie().padAngle(padAngle).sort(null).value(i => V[i])(I);
				const arc = d3.arc().innerRadius(innerRadius).outerRadius(outerRadius);
				const arcLabel = d3.arc().innerRadius(labelRadius).outerRadius(labelRadius);

				const svg = d3.select("#svg_right")
					.attr("viewBox", [-340, -150, 660, 330])
				// .attr("style", "max-width: 100%; height: auto; height: intrinsic;");
				// 
				svg.append("g")
					.attr("stroke", stroke)
					.attr("stroke-width", strokeWidth)
					.attr("stroke-linejoin", strokeLinejoin)
					.selectAll("path")
					.data(arcs)
					.join("path")
					.attr("fill", d => {
						d.color = color(N[d.data])
						return color(N[d.data])
					})
					.attr("d", arc)
					.append("title")
					.text(d => title(d.data));
				// console.log(arcs)
				svg.append("g")
					.attr("font-family", "sans-serif")
					.attr("font-size", 12)
					.attr("fill", "#000000")
					.attr("text-anchor", "middle")
					.selectAll("text")
					.data(arcs)
					.join("text")
					.attr("transform", d => `translate(${arcLabel.centroid(d)})`)
					.selectAll("tspan")
					.data(d => {
						const lines = `${title(d.data)}`.split(/\n/);
						// console.log(lines.slice(0, 1))
						// return (d.endAngle - d.startAngle) > 0.25 ? lines : lines.slice(0, 1);
						return lines.slice(0, 1)

					})

					.join("tspan")
					.attr("x", 0)
					.attr("y", (_, i) => `${i * 1.1}em`)
					.attr("font-weight", (_, i) => i ? null : "bold")
					.text(function(d, i) {
						var res = d
						if (d.length >= 7) {
							res = d.substring(0, 7) + "..."
						}
						return res
					});

				// console.log(data.column)

				svg.append("g")
					.selectAll("g")
					.data(data)
					.join("g")
					.attr("transform", (d, i) => `translate(-55,${i * 30-140})`)
					.call(g => g.append("rect")
						.attr("width", 18)
						.attr("height", 18)
						.attr("fill", function(d, i) {
							// console.log(color(N[d.data]))
							if (i > 9) {
								return 'none'
							} else {
								return color(N[i])
							}
						}))
					.call(g => g.append("text")
						.attr("x", 24)
						.attr("y", 9)
						.attr("dy", "0.35em")
						.text(function(d, i) {
							if (i == 9) {
								return "........."
							}
							if (i > 9) {
								return ""
							} else {
								if (d.name.length >= 7) {
									d.name = d.name.substring(0, 7) + "..."
								}
								return d.name
							}

						})
						.attr("fill", () => '#fff') //
						.attr("font-size", "14px")
						.attr("font-weight", "bolder")
					)

				return Object.assign(svg.node(), {
					scales: {
						color
					}
				});
			},
			create_graph() {
				var _this = this
				var forceCollide = d3.forceCollide()
					.radius(d => {
						return 70
					})
					.iterations(0.15)
					.strength(0.75)
				var data_origin = this.data
				var data = data_origin
				const root = d3.hierarchy(data);
				const links = root.links();
				const nodes = root.descendants();
				const simulation = d3.forceSimulation(nodes)
					.force("link", d3.forceLink(links).id(d => d.id).distance(200).strength(0.8))
					.force("charge", d3.forceManyBody(-100).strength(-50))
					.force("x", d3.forceX())
					.force("y", d3.forceY())
					.force("collision", forceCollide);

				this.addMarkers()
				const svg = d3
					.select("#svg_left")
					.attr("viewBox", [-350, -150, 700, 350])

				const link = svg.append("g")
					.selectAll("line")
					.data(links)
					.join("line")
					.attr("stroke", "#999")
					.attr("stroke-width", "5px")
					.attr("stroke-opacity", 0.6)
					.join("path") //joind3 =enter.append("").attr("","").exit.remove()
					.attr("marker-end", "url(#posMarker)");

				const g_node = svg
					.append("g")
					.selectAll("g.eachnode")
					.data(nodes)
				const g_link = svg
					.append("g")
					.selectAll("g.eachnode")
					.data(links)

				const each_node = g_node
					.enter()
					.append("g")
					.attr("class", "eachnode")
				const each_link = g_link
					.enter()
					.append("g")
					.attr("class", "eachlink")

				const node = each_node
					.append("circle")
					// .attr("fill", d => d.children ? "#59aa40" : "#32a7b1")
					.attr("fill", function(d) {
						if (d.children) {
							return "#32a7b1";
						} else {
							// console.log(d.data.relation_name)
							var labels = _this.labels
							var colors = _this.colors
							for (let i = 0; i < labels.length; i++) {
								if (d.data.relation_name == labels[i]) {
									return colors[i];
								}
							}
							return colors[7]
						}
					})
					.attr("stroke", d => d.children ? null : "#fff")
					.attr("stroke-width", "3px")
					.attr("r", 40)
					.call(this.drag(simulation));


				const text = each_node
					.append("text")
					.attr("font-size", () => 14) //
					.attr("fill", () => '#fff') // 
					.attr("font-weight", "bold")
					.attr('name', d => d.data.name) // 
					.attr("text-anchor", "middle") //
					.attr("x", function(d) {
						return textBreaking(d3.select(this), d.data.name)
					})
					.call(this.drag(simulation));

				const linksName = each_link
					.append("text")
					.attr('text-anchor', 'middle')
					.attr('fill', '#ebebeb')
					.attr("font-size", () => 12)
					.attr('font-weight', 'bold')
					.text(d => {
						var string = d.target.data.relation_name
						if (string.length > 8) {
							string = string.substring(0, 8) + "..."
						}
						return string
					})


				node.append("title") //title
					.text(d => d.data.name)
					.attr("fill", () => '#fff') //
					.attr("font-weight", "normal")
					.attr("text-anchor", "middle") // 

				simulation.on("tick", () => {
					link
						.attr("x1", d => d.source.x)
						.attr("y1", d => d.source.y)
						.attr("x2", d => d.target.x)
						.attr("y2", d => d.target.y);

					node
						.attr("cx", d => d.x)
						.attr("cy", d => d.y);
					text.attr('transform', function(d) {
						return 'translate(' + d.x + ',' + (d.y + 5) + ')'
					})
					linksName
						.attr('transform', d => {
							let x = Math.min(d.source.x, d.target.x) + Math.abs(d.source.x - d.target.x) / 2
							let y = Math.min(d.source.y, d.target.y) + Math.abs(d.source.y - d.target.y) / 2 -
								1
							// tanA = a / b
							// A = arctan(tanA)
							let tanA = Math.abs(d.source.y - d.target.y) / Math.abs(d.source.x - d.target.x)
							let angle = Math.atan(tanA) / Math.PI * 180
							// let angle = Math.atan2(1,1)/Math.PI*180
							// console.log(angle)
							// 
							if (d.source.x > d.target.x) {
								// 
								if (d.source.y <= d.target.y) {
									angle = -angle
								}
								// else {  // 
								//   angle = angle
								// }
							} else if (d.source.y > d.target.y) {
								// 
								angle = -angle
							}
							return 'translate(' + x + ',' + (y - 10) + ')' + 'rotate(' + angle + ')'
						})
				});

				function textBreaking(d3text, text) {
					// console.log("text")
					// console.log(text)
					const len = text.length
					if (len <= 4) {
						d3text.append('tspan')
							.attr('x', 0)
							.attr('y', 2)
							.text(text)
					} else {
						const topText = text.substring(0, 3) //3
						const midText = text.substring(3, 7) //4
						let botText = text.substring(7, len) //4
						let topY = -16
						let midY = 0
						let botY = 16
						if (len <= 7) {
							topY += 10
							midY += 10
						} else if (len > 10) {
							botText = text.substring(7, 9) + '...' // 10 
						}

						d3text.text('')
						d3text.append('tspan')
							.attr('x', 0)
							.attr('y', topY)
							.text(function() {
								return topText
							})
						d3text.append('tspan')
							.attr('x', 0)
							.attr('y', midY)
							.text(function() {
								return midText
							})
						d3text.append('tspan')
							.attr('x', 0)
							.attr('y', botY)
							.text(function() {
								return botText
							})
					}
				}
			},
			drag(simulation) {

				function dragstarted(event, d) {
					if (!event.active) simulation.alphaTarget(0.3).restart();
					d.fx = d.x;
					d.fy = d.y;
				}

				function dragged(event, d) {
					d.fx = event.x;
					d.fy = event.y;
				}

				function dragended(event, d) {
					if (!event.active) simulation.alphaTarget(0);
					d.fx = null;
					d.fy = null;
				}

				return d3.drag()
					.on("start", dragstarted)
					.on("drag", dragged)
					.on("end", dragended);
			},
			async GetNeo4jData(limit_items, keywords) {
				//  records
				// json 
				//  neo4jjson data
				const db = { // 
					// url: 'bolt://127.0.0.1:7687/', // bolt localhost
					url: 'bolt://123.56.133.187:7687/', //httphttps
					user: 'neo4j', // 		(neo4j)
					password: 'neo4jneo4j' // 		ne4oj
				}
				const start = new Date()
				const neo4j = require('neo4j-driver')
				const driver = neo4j.driver(db.url, neo4j.auth.basic(db.user, db.password))
				const session = driver.session()

				// const result = await session.run(
				// 	'MATCH p = ()-[]->() ' +
				// 'RETURN p ' +'LIMIT $limit_items ', {
				// 	limit_items: neo4j.int(limit_items)
				// }
				// );
				const result = await session.run(
					'match p=(n{name:' + "'" + keywords + "'" + '})-[]-()return p ' +
					'LIMIT $limit_items ', {
						limit_items: neo4j.int(limit_items)
					}
				);

				var number = result.records.length
				console.log('' + number.toString())
				console.log(result)
				var res = {}
				var children = []
				res["name"] = keywords
				for (var i = 0; i < number; i++) {
					var tmp = {}
					tmp["name"] = result.records[i].get('p').segments[0].end.properties.name
					tmp["relation_type"] = result.records[i].get('p').segments[0].relationship.type
					tmp["relation_name"] = result.records[i].get('p').segments[0].relationship.properties.name
					children.push(tmp)
				}
				res["children"] = children
				console.log(res)
				this.label = children
				this.data = res
				return number
			},
			zhTransform(str, type) {
				let zh_cn_str =
					'';
				let zh_tw_str =
					'';
				// 
				let result_str = '';
				// 
			 for (let i = 0; i < str.length; i++) {
					// 
					let chat = str.charAt(i);
					// 
					if (type == 'simplified') {
						// 
						let str_index = zh_tw_str.indexOf(chat);
						// 
						result_str += str_index < 0 ? chat : zh_cn_str.charAt(str_index);
					} else if (type == 'traditional') {
						// 
						let str_index = zh_cn_str.indexOf(chat);
						// 
						result_str += str_index < 0 ? chat : zh_tw_str.charAt(str_index);
					}
				}
				return result_str;
			},
			wiki_api(search_param) {
				var that = this
				var url = "https://en.wikipedia.org/w/api.php";
				var zh_url =
					"https://zh.wikipedia.org/w/api.php?format=json&action=query&generator=search&gsrnamespace=0&gsrlimit=10&prop=pageimages|extracts&pilimit=max&exintro&explaintext&exsentences=1&exlimit=max&origin=*&gsrsearch=";
				//https://en.wikipedia.org/w/api.php?action=query&list=search&srsearch=5G&utf8=1&format=json

				var params = {
					action: "query",
					list: "search",
					srsearch: "Nelson Mandela",
					format: "json"
				};

				var test = "http://en.wikipedia.org/wiki/" + encodeURIComponent("5G")
				console.log(test)

				// Object.keys(params).forEach(function(key) {
				// 	url += "&" + key + "=" + params[key];
				// });
				// var url_params = {
				// 	format: "json",
				// 	action: "query",
				// 	generator:"search",
				// 	gsrnamespace:"0",
				// 	gsrlimit:"10",
				// 	prop:"pageimages|extracts",
				// 	pilimit:"max",
				// 	// exintro : "",
				// 	// explaintext : "",
				// 	exsentences : "10",
				// 	exlimit : "max",
				// 	gsrsearch:"fuck"
				// }
				zh_url = zh_url + search_param
				console.log(zh_url)
				fetch(zh_url)
					.then(function(response) {
						// console.log(response.json())
						return response.json().then(function(json) {
							var each_result = []
							var ids = [];
							for (var pageid in json.query.pages) {
								ids.push(pageid);
							}
							var query_result = json.query.pages
							for (let i = 0; i < ids.length; i++) {
								var tmp = json.query.pages[ids[i]]
								var title = json.query.pages[ids[i]].title
								var url = "http://zh.wikipedia.org/wiki/" + encodeURIComponent(tmp.title);
								var img_url = ""
								var listcontent = "";
								var extract = json.query.pages[ids[i]].extract
								if (tmp.thumbnail) {
									img_url = tmp.thumbnail.source
									listcontent = "<img src='" + img_url + "'>";
								}
								tmp["url"] = url
								tmp["img_url"] = img_url
								each_result.push(tmp)
								// console.log(document.getElementById(i))
								var list = $(".labels ol li ").eq(i);
								console.log(extract.length)
								if (extract.length > 100) {
									extract = extract.substring(0, 150) + "......"
								}
								title = that.zhTransform(title, "simplified")
								extract = that.zhTransform(extract, "simplified")
								list.find('a').text(title).attr('href', url);
								list.find('p').html(listcontent);
								list.find('.clearfix').text(extract);
							}
							console.log(each_result)
						})
					})
					.then(function(response) {
						if (response.query.search[0].title === "Nelson Mandela") {
							console.log("Your search page 'Nelson Mandela' exists on English Wikipedia");
						}
					})
				// .catch(function(error) {
				// 	const h = that.$createElement;
				// 		that.$message({
				// 			message: h('p', null, [
				// 				h('span', {
				// 					style: 'color:teal'
				// 				}, 'wiki :  '),
				// 				h('i', {
				// 					style: 'color: red'
				// 				}, "VPN")
				// 			]),
				// 			center: true,
				// 			type: 'error'
				// 		});
				// 		console.log(error);
				// 	});

			},

		},

		mounted() {
			this.loadAll()
