<template>
  <div class = "container">
<!-- 	<div style="margin-top: 20px;width: 500px;">
	    <el-autocomplete
	      style="width: 500px"
	      class="inline-input"
	      v-model="input"
	      :fetch-suggestions="querySearch"
	      placeholder=""
	      :trigger-on-focus="true"
	      @select="handleSelect"
		  @clear = "input_clear"
	      clearable>
	      <el-button
	        slot="append"
	        type="success"
	        icon="el-icon-search"
	        @click="query"
	      ></el-button>
	    </el-autocomplete>
	</div> -->
	<keep-alive>
		<svg style="border:1px dashed grey"
		  id="svg"
		  width="1400"
		  height="760"
		></svg>
	</keep-alive>
	<div class="node_info hidden">
	    <p>
	        <strong class="name"></strong>
	    </p>
	</div>
  </div>
</template>

<script>
import * as d3 from 'd3'
import install from '@/plugins/d3-context-menu'
install(d3) // d3
export default {
  name: 'graph',
  props: {
    data: {
      type: Object,
      default: function () {
        return {
          nodes: [],
          links: []
        }
      }
    },
    /* eslint-disable */
    // 
    // names		
    // labels		records.json
    names: {
      type: Array
    },
    labels: Array,
    linkTypes: Array
  },
  data () {
    return {
      svgDom: null, // svgDOM => d3.select('#svg1')
      keywords: '',
      nodeState: 0,
      // 0/1
      textState: 0,
      // d3render()
      nodes: [],
      links: [],
      /* eslint-disable */
      // 
      // colors		9
      // states   on / off
      colors: ['#BA55D3','#55cccc', '#aaaaff', '#4e88af', '#d0d066','#59aa40', '#1E90FF', '#ff8c8e','#32a7b1','#ca635f','#a5a5a5'],
      states: [],
      selectNodeData: {}, // 
      isNodeClicked: false, // 
      temp: {}, // 
      dialogFormVisible: false,
      isEdit: true,
	  input: '',
	  mode: '1',
	  input_results:[],
	  on_click:false,
    }
  },

  watch: {
    // 
    data (newData, oldData) {
      console.log(newData, oldData)
      // svg
      this.svgDom.on('.', null)
      this.svgDom.selectAll('*').on('.', null)
      this.d3init()
    }
  },
  created () {
    // this.states = Array(this.names.length).fill('on')
  },
  mounted () {
	this.input_results = this.loadAll()
    this.d3init()
	
  },
  beforeDestroy () {
    // svg
    this.svgDom.on('.', null)
    this.svgDom.selectAll('*').on('.', null)
  },
  methods: {
	  loadAll () {
	    return [
	    { value: 'not supported 5QI value', address: 'null' }
	    ]
	  },
	  query () {
		var query_name = this.input
		console.log(":"+query_name)
		var query_nodes = this.nodes
		for (let item of query_nodes) {
			console.log(item)
		  if (item.properties.name == query_name) {
		    console.log("")
			this.isNodeClicked = true
			this.changeGraphStyle(query_name)
			const h = this.$createElement;
			    this.$message({
			        message: h('p', null, [
			            h('span', null, ' :  '),
			            h('i', { style: 'color: teal' }, query_name)
			        ]),
					center: true,
					type: 'success'
			     });
			 return 
		  }
		}
		const h = this.$createElement;
		this.$message({
		    message: h('p', null, [
		        h('span', {style:'color:red'}, ' :  '),
		        h('i', { style: 'color: red' }, query_name)
		    ]),
			center: true,
			type: 'error'
		 });
	  },
	  querySearch (queryString, cb) {
	    var res = this.input_results
	    var results = queryString ? res.filter(this.createFilter(queryString)) : res
	    //  callback 
	    cb(results)
	  },
	  createFilter (queryString) {
	    return (res) => {
	      return (res.value.toLowerCase().indexOf(queryString.toLowerCase()) !== -1)
	    }
	  },
	  handleSelect (item) {
	    console.log(item)
	  },
	  input_clear(){
		  this.$message({
		    message:'',
		  	center: true,
		   });
	  },
    // d3
    d3init () {
      this.links = this.data.links
      this.nodes = this.data.nodes
	  console.log(this.nodes)
	  console.log(this.links)
      this.svgDom = d3.select('#svg')  // svgDOM
      // this.d3jsonParser(this.graph)
      this.d3render()
      // 
      this.stateInit()
    },
    // 
    stateInit () {
      this.nodeState = 0
      this.textState = 0
      // console.log(this.names)
      this.states = Array(this.names.length).fill('on')
    },
    d3render () {
      var _this = this // Vued3this
      // svg
      _this.svgDom.selectAll('*').remove()
      // svg.selectAll('g').remove()
      var svg = _this.svgDom
        .on('click', () => { 
          // console.log(this.isNodeClicked)
          this.isNodeClicked = false
          // 
          this.clearGraphStyle()
          // 
          if(this.keywords !== '') {
            this.searchKeyWords()
          }
        })
        // zoom
        .call(d3.zoom().on('zoom', event => {
          // console.log(event)
          var scale = event.transform.k,
              translate = [event.transform.x, event.transform.y]
          svg.attr('transform', 'translate(' + translate[0] + ', ' + translate[1] + ') scale(' + scale + ')');
        }))
        .append('g')
        .attr('width', '100%')
        .attr('height', '100%')
		
		d3.select("#svg").attr("viewBox", [-850, -120, 1800, 900])
        // 
		
      this.addMarkers()
	  
      // 
      var forceCollide = d3.forceCollide()
        .radius(d => { return 50 })
        .iterations(0.1)
        .strength(0.75)
	  //
	  //centerlinkchargecollision
      //d3.forceSimulation() linkcharge 
	  //d3.forceSimulation([nodes])  nodes  forces() // nodes  starts()
	  //simulation.force(name[, force]) link 
	  //d3.forceLink([links])  links  links  link.id([id]) id 
	  //d3.forceManyBody()  manyBody.strength([strength])  
      //d3.forceCenter([x, y]) x-  y-  x  y  0,0.
	  //svg.node() svg  svg
	  // null
	  var simulation = d3.forceSimulation(this.nodes)    												 
        .force("link", d3.forceLink().id(d => d.id))     
        .force("charge", d3.forceManyBody().strength(-200))  //charge link center 
        .force("x", d3.forceX())
        .force("y", d3.forceY())
		// .force("center", d3.forceCenter(width / 2, height / 2)
        // .force("center", d3.forceCenter(svg.node().parentElement.clientWidth / 2, svg.node().parentElement.clientHeight / 2))
		// .force("collision", forceCollide)
      // D3HTML
      // g,selectALLline,data(graph.links),enter().append("line")
      // ,
	  // console.log("simulation")
	  // console.log(simulation)
	  //stroke-width 
      var link = svg.append("g")
        .attr("class", "links")
        .selectAll("line")
        .data(this.links).enter()
        .append("line")
        .attr("stroke-width", function(d) {
          // links
          return 3+"px" //2
        })		      //  
        .join("path") //joind3 =enter.append("").attr("","").exit.remove()
        .attr("marker-end", "url(#posMarker)")
			
      var linksName = svg.append("g")
        .attr("class", "linkTexts")
        .selectAll("text")
        .data(this.links)
        .join("text")
        .attr('text-anchor','middle')
        .attr('fill', '#444444')
        .attr('font-size', '10px')
        .attr('font-weight', 'bold')
		.text(d => d.properties.name)

      // 
      // selectAll("circle"),d.size
      // ,,,d.id
      // call(),dragstarted
	  //r
      var node = svg.append("g")
        .attr("class", "nodes")
        .selectAll("circle")
        .data(this.nodes).enter()
        .append("circle").attr("r", function(d) {
          // nodes
          // console.log(d)
          let size = 16
          switch(d.label){
            case _this.labels[0]: break;
            case _this.labels[1]: size = 14; break;
            case _this.labels[2]: size = 13; break;
            default: size = 13; break;
          }
          return size * 2
        })
        .attr("fill", d => {
          for (let i = 0;i < this.labels.length;i++) {
            if (d.label === this.labels[i]) return this.colors[i]
          }
        })
        .attr("stroke", "none")
        .attr("name", d => d.properties.name)
        .attr("id", d => d.id)
        .call(this.drag(simulation))
        .on("click", nodeClick)
        .on('mouseenter', function (event,d) {
			console.log(_this.isNodeClicked)
          if(_this.isNodeClicked == false){
			  const node = d3.select(this)
			  let name = node.attr("name")
			  let id = node.attr("id")
			  let color = node.attr('fill')
			  console.log(name, id, color)
			  //#info h4name
			  _this.$set(_this.selectNodeData, 'id', id)
			  _this.$set(_this.selectNodeData, 'name', name)
			  _this.$set(_this.selectNodeData, 'color', color)
			  // console.log(_this.selectNodeData)
			  
			  //id
			  for (let item of _this.nodes) {
			    if (item.id == id) {
			      // for(var key in item.properties)
			      _this.$set(_this.selectNodeData, 'properties', item.properties)
			    }
			  }
			  // 
			  _this.changeGraphStyle(name)
			  // _this.show_NodeInfo(event,d)
		  }
        })
        .on('mouseleave', event => {
          console.log(""+this.isNodeClicked.toString())
          if (!this.isNodeClicked) {
            this.clearGraphStyle()
            // 
            if(this.keywords !== '') {
              this.searchKeyWords()
            }
          }
		  // this.hide_NodeInfo(event)
        })
        .on('contextmenu', d3.contextMenu(this.menu))    
      // 
      // text()
      //  attr("text-anchor", "middle")
      var text = svg.append("g")
        .attr("class", "texts")
        .selectAll("text")
        .data(this.nodes)
        .enter()
        .append("text").attr("font-size", () => 10)    //
        //.attr("fill", () => '#fff')         		   // 
		.attr("font-weight","normal")
        .attr('name', d => d.properties.name)		   // 
        .attr("text-anchor", "middle")				   // 
        .attr('x', function (d) {
          return textBreaking(d3.select(this), d.properties.name)
		  // return textBreaking(d3.select(this), d.properties.name)
        })
        .call(this.drag(simulation))
        .on("click", nodeClick)
        .on('mouseenter', function (event,d) {
			if(_this.isNodeClicked == false){
				const text = d3.select(this)
				let name = text.attr("name")
				_this.$set(_this.selectNodeData, 'name', name)
				
				// id
				for (let item of _this.nodes) {
				  if (item.properties.name == name) {
				    // id
				    _this.$set(_this.selectNodeData, 'id', item.id)
				    _this.$set(_this.selectNodeData, 'properties', item.properties)
				    // label
				    let index = 0
				    switch (item.label) {
				      case _this.labels[0]: break;
				      case _this.labels[1]: index = 1;break;
				      case _this.labels[2]: index = 2;break;
				      default: index = 3;break;
				    }
				    _this.$set(_this.selectNodeData, 'color', _this.colors[index])
				  }
				}
				_this.changeGraphStyle(name)
				// _this.show_NodeInfo(event,d)
			}
         
        })
        .on('mouseleave', (event) => {
          if(!this.isNodeClicked) {
            this.clearGraphStyle()
            // 
            if(this.keywords !== '') {
              this.searchKeyWords()
            }
          }
		  // this.hide_NodeInfo(event)
        })
        .on('contextmenu', d3.contextMenu(this.menu))
        // .call(d3.drag()
        //   .on("start", dragstarted)
        //   .on("drag", dragged)
        //   .on("end", dragended)
        // )

      // title
      // node.append("title").text(d => d.properties.name)
      node.append("title").text(d => d.properties.name)
      // simulationticked
      simulation.on("tick", ticked)
        
      simulation.force("link")
        .links(this.links)
        .distance(d => { // 
          let distance = 60
          // switch(d.source.label) {
          //   case _this.labels[0]: distance += 30;break;
          //   case _this.labels[1]: distance += 25;break;
          //   case _this.labels[2]: distance += 22;break;
          //   default: distance += 20;break; 
          // }
          // switch(d.target.label) {
          //   case _this.labels[0]: distance += 30;break;
          //   case _this.labels[1]: distance += 25;break;
          //   case _this.labels[2]: distance += 22;break;
          //   default: distance += 20;break; 
          // }
          return distance * 2
        })
      
      /****************************************** 
       * 
       * ticked
       */
      // ticked()linkxy node translate
      function ticked() {
        link
          .attr("x1", d => d.source.x)
          .attr("y1", d => d.source.y)
          .attr("x2", d => d.target.x)
          .attr("y2", d => d.target.y)
    
        linksName
          .attr('transform', d => {
            let x = Math.min(d.source.x, d.target.x) + Math.abs(d.source.x - d.target.x) / 2
            let y = Math.min(d.source.y, d.target.y) + Math.abs(d.source.y - d.target.y) / 2 - 1
            // tanA = a / b
            // A = arctan(tanA)
            let tanA = Math.abs(d.source.y - d.target.y) / Math.abs(d.source.x - d.target.x)
            let angle = Math.atan(tanA) / Math.PI * 180
            // let angle = Math.atan2(1,1)/Math.PI*180
            // console.log(angle)
            // 
            if (d.source.x > d.target.x) {
              // 
              if (d.source.y <= d.target.y) {
                angle = -angle
              }
              // else {  // 
              //   angle = angle
              // }
            } else if (d.source.y > d.target.y) {
              // 
              angle = -angle
            }
            return 'translate(' + x + ',' + y + ')' + 'rotate(' + angle + ')'
          })

        node
          .attr("cx", d => d.x)
          .attr("cy", d => d.y)
    
        text.attr('transform', function(d) {
          let size = 15
          switch(d.label){
            case _this.labels[0]: break;
            case _this.labels[1]: size = 14;break;
            case _this.labels[2]: size = 13;break;
            default: size = 12;break;
          }
          size -= 5
          return 'translate(' + (d.x - size / 2 + 3) + ',' + (d.y + size / 2) + ')'
        })
      }
      
      /**
       * 10
       * @method textBreaking
       * @param {d3text} DOM
       * @param {text} 
       * @return {void}
       */
      function textBreaking(d3text, text) {
		// console.log("text")
		// console.log(text)
        const len = text.length
        if (len <= 3) {
          d3text.append('tspan')
            .attr('x', 0)
            .attr('y', 2)
            .text(text)
        } 
		else {
          const topText = text.substring(0, 3)    //3
          const midText = text.substring(3, 7)	  //4
          let botText = text.substring(7, len)	  //4
          let topY = -16
          let midY = 0
          let botY = 16
          if (len <= 7) {
            topY += 10
            midY += 10
          } else if (len > 10){
            botText = text.substring(7, 9) + '...' // 10 
          }
  
          d3text.text('')
          d3text.append('tspan')
            .attr('x', 0)
            .attr('y', topY)
            .text(function () {
              return topText
            })
          d3text.append('tspan')
            .attr('x', 0)
            .attr('y', midY)
            .text(function () {
              return midText
            })
          d3text.append('tspan')
            .attr('x', 0)
            .attr('y', botY)
            .text(function () {
              return botText
            })
        }
      }
      // 
      // mouseenterid
      //      this.selectNodeData
      // 
      function nodeClick(event, d) {
        event.cancelBubble = true
        event.stopPropagation() // 
        
        const name = _this.selectNodeData.name
        _this.isNodeClicked = true
        _this.changeGraphStyle(name)

        return false
      }
    },
	show_NodeInfo(event,d){
		d3.select('.node_info').transition().duration(300).style('opacity', 1).style('display', 'block')
		// console.log("event")
		// console.log(event)     
		var yPosition = event.layerY +50;  //  y
		var xPosition = event.layerX +200;  //	 x
		// 
		var node_info = d3.select(".node_info").style("left", xPosition + "px").style("top", yPosition + "px");
		node_info.select(".name").html(''+d.properties.name+'<br>'+'ID'+d.id+'<br>'+'value'+"null");
		      // hidden
		node_info.classed("hidden", false);
	},
	hide_NodeInfo(event){
		d3.select('.node_info').style('opacity', 0).style('display', 'none')
	},
    // 
    changeGraphStyle (name) {
      // console.log(this.isNodeClicked)
      // #svg1 .nodescircleclass
      this.svgDom.select('.nodes').selectAll('circle').attr('class', d => {
        // namenamefixed
        if(d.properties.name == name) {
          return 'fixed'
        }
        // (CSS)
        else {
          // links,idname
          // : graph = data
          for (var i = 0; i < this.links.length; i++) {
            // linksname
            if (this.links[i]['source'].properties.name == name && this.links[i]['target'].id == d.id) {
                return 'active'
            }
            if (this.links[i]['target'].properties.name == name && this.links[i]['source'].id == d.id) {
                return 'active'
            }
          }
          return this.isNodeClicked ? 'inactive' : ''
        }
      })
      // 
      this.svgDom.select('.texts').selectAll('text')
        .attr('class', d => {
          // namenamefixed
          if(d.properties.name == name) {
            return ''
          }
          // (CSS)
          else {
            // links,idname
            // : graph = data
            for (var i = 0; i < this.links.length; i++) {
              // linksname
              if (this.links[i]['source'].properties.name == name && this.links[i]['target'].id == d.id) {
                  return ''
              }
              if (this.links[i]['target'].properties.name == name && this.links[i]['source'].id == d.id) {
                  return ''
              }
            }
            return this.isNodeClicked ? 'inactive' : ''
          }
        })
      // line  || 
      this.svgDom.select(".links").selectAll('line')
        .attr('class', d => {
          if (d.source.properties.name == name || d.target.properties.name == name) {
              return 'active'
          } else {
              return this.isNodeClicked ? 'inactive' : ''
          }
        })
        .attr('marker-end', d => {
          if (d.source.properties.name == name || d.target.properties.name == name) {
            return 'url(#posActMarker)'
          } else {
            return 'url(#posMarker)'
          }
        })
      //   || 
      this.svgDom.select(".linkTexts").selectAll('text')
        .attr('class', d => {
          if (d.source.properties.name == name || d.target.properties.name == name) {
              return 'active'
          } else {
              return this.isNodeClicked ? 'inactive' : ''
          }
        })
    },
    clearGraphStyle () {
      // 
      this.svgDom.select('.nodes').selectAll('circle').attr('class', '')
      this.svgDom.select(".texts").selectAll('text').attr('class', '')
      this.svgDom.select('.links').selectAll('line').attr('class', '').attr('marker-end', 'url(#posMarker)')
      this.svgDom.select(".linkTexts").selectAll('text').attr('class', '')
      // d3.select(this).attr('class', '')
    },
    drag(simulation) {
      function dragsubject(event) {
        return simulation.find(event.x, event.y);
      }

      function dragstarted(event) {
        if (!event.active) simulation.alphaTarget(0.3).restart();
        event.subject.fx = event.subject.x;
        event.subject.fy = event.subject.y;
      }
      
      function dragged(event) {
        event.subject.fx = event.x;
        event.subject.fy = event.y;
      }
      
      function dragended(event) {
        if (!event.active) simulation.alphaTarget(0);
        //
        event.subject.fx = null;
        event.subject.fy = null;
      }
      
      return d3.drag()
        .subject(dragsubject)
        .on("start", dragstarted)
        .on("drag", dragged)
        .on("end", dragended)
    },
    // ()
    addMarkers() {
      // 
      var defs = this.svgDom.append("defs")
      const posMarker = defs.append("marker")
        .attr("id", "posMarker")
        .attr("orient", "auto")
        .attr("stroke-width", 2)
        .attr("markerUnits", "strokeWidth")
        .attr("markerUnits", "userSpaceOnUse")
        .attr("viewBox", "0 -5 10 10")
        .attr("refX", 31)
        .attr("refY", 0)
        .attr("markerWidth", 12)
        .attr("markerHeight", 12)
        .append("path")
        //.attr("d", "M 0 -5 L 10 0 L 0 5")
		.attr("d","M 3 5 L 8 0 L 3 -5")
        .attr('fill', '#ffffff')
        .attr("stroke-opacity", 0.6);
      const posActMarker = defs.append("marker")   //
        .attr("id", "posActMarker")
        .attr("orient", "auto")
        .attr("stroke-width", 2)
        .attr("markerUnits", "strokeWidth")
        .attr("markerUnits", "userSpaceOnUse")
        .attr("viewBox", "0 -5 10 10")
        .attr("refX", 31)
        .attr("refY", 0)
        .attr("markerWidth", 12)
        .attr("markerHeight", 12)
        .append("path")
       .attr("d","M 0 5 L 8 0 L 0 -5")
        .attr('fill', '#ffd918')
        .attr("stroke-opacity", 0.6);
      // const negMarker = defs.append("marker")
      //   .attr("id","negMarker")
      //   .attr("orient","auto")
      //   .attr("stroke-width",2)
      //   .attr("markerUnits", "strokeWidth")
      //   .attr("markerUnits", "userSpaceOnUse")
      //   .attr("viewBox", "0 -5 10 10")
      //   .attr("refX", -25)
      //   .attr("refY", 0)
      //   .attr("markerWidth", 12)
      //   .attr("markerHeight", 12)
      //   .append("path")
      //   .attr("d", "M 10 -5 L 0 0 L 10 5")
      //   .attr('fill', '#999')
      //   .attr("stroke-opacity", 0.6);
    }
  }
}
</script>
<style type="text/css">
	.container {
	  position: relative;
	  border: 10px #000 solid;
	  background-color: #fcf9f8;
	  display: flex;
	  flex-direction: column;
	  align-items: center;
	  justify-content: center;
	  overflow: hidden;
	}
</style>

<style lang="scss">
@import '@/plugins/d3-context-menu';
$opacity: 0.15;  /*  */
$activeColor: #1E90FF;  /*  */
svg {
  margin: 20px 0px;
  // border: 1px #000 solid;
}
/**/
.links line {
  stroke: #b6c5cdb2; // #bbb
  stroke-opacity: 1;
  &.inactive {
    /* display: none !important; */
    opacity: $opacity;
  }
  &.active {
    stroke: #333333;
    stroke-width: 5px;
  }
  &.hide {
    display: none !important;
  }
}
.nodes circle {
   stroke: #dcdcdc;
   stroke-width: 3px;
   //opacity: 1;
  // box-shadow: 4px 4px 15px #181bff;;
  &.fixed {
    // fill: rgb(102, 81, 81);
    stroke: #93d3b0;  // #888;
    stroke-width: 14px;
    stroke-opacity: $opacity + 0.3;
    border: 10px #000 solid;
  }
  &.inactive {
    /* display: none !important; */
    opacity: $opacity;
  }
  &.active {
    stroke: #ffffff;
    stroke-width: 4px;
  }
  &:hover {
    cursor: pointer;
  }
  &.hide {
    display: none !important;
  }
}
.texts text {
  fill: rgb(255, 255, 255);
  cursor: pointer;
  text-decoration: none;
  user-select: none;
  
  &:hover {
    cursor: pointer;
  }
  &.inactive {
    /* display: none !important; */
    opacity: $opacity;
  }
}
.linkTexts text {
  //fill: rgb(0, 0, 0);
  //stroke: #fef7ffb2; // #bbb
  //stroke-opacity: 1;
  &.active {
    // stroke: #ffffff;
	fill:#ffffff;
  }
  &.inactive {
    /* display: none !important; */
    opacity: $opacity;
  }
}
